.chat {
    flex: 2;

    .chat-info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        background-color: #5d5b8d;
        color: lightgray;
        padding: 0px 12px;
    
        .chat-icons {
            display: flex;
            gap: 16px;

            svg {
                cursor: pointer;
            }
        }
    }
}