$border-radius: 15px;

.message {
    display: flex;
    // align-items: flex-end;
    gap: 12px;
    .message-info {
        display: flex;
        align-items: flex-end;
        height: 100%;
        img {
            width: 36px;
            height: 36px;
            border-radius: 50%;
            object-fit: center;
        }
    }

    .message-content {
        overflow: hidden;
        max-width: 70%;
        border: none;
        height: 100%;

        img {
            height: 100%;
            max-width: 100%;
            object-fit: cover;
        }

        p {
            max-width: max-content;
            padding: 12px;
            line-height: 1.4;
        }
    }

    &.owner {
        flex-direction: row-reverse;
        .message-content {
            border-top-left-radius: $border-radius;
            border-bottom-left-radius: $border-radius;

            p {
                background-color: rgb(219, 190, 190);
            }
        }
    }

    &:not(.owner) {
        .message-content {
            border-top-right-radius: $border-radius;
            border-bottom-right-radius: $border-radius;

            p {
                background-color: lightblue;
            }
        }
    }
}

.message:not(.owner) + .owner,
.owner + .message:not(.owner) {
    .message-content {
        border-top-right-radius: $border-radius;
        border-top-left-radius: $border-radius;
    }
}

// hide icons, lower spacing
.message:not(.owner):has(+ .message:not(.owner)),
.owner:has(+ .owner) {
    .message-info {
        img {
            opacity: 0;
        }
    }

    margin-bottom: -9px;
}

// 
.message:not(.owner):has(+ .owner),
.owner:has(+ .message:not(.owner)) {
    .message-content {
        border-bottom-left-radius: $border-radius;
        border-bottom-right-radius: $border-radius;
    }
}

.message:first-child {
    .message-content {
        border-top-left-radius: $border-radius;
        border-top-right-radius: $border-radius;
    }
}

.message:last-child {
    .message-content {
        border-bottom-left-radius: $border-radius;
        border-bottom-right-radius: $border-radius;
    }
}


// Tippy

.tippy {
    background-color: #ccc;
    padding: 6px 8px;
    border-radius: 3px;
}




