$primary-color: #5d5b8d;

@mixin flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.form-container {
    @include flex-center();

    height: 100vh;
    background-color: lightblue;

    .form-wrapper {
        @include flex-center();
        flex-direction: column;
        background-color: #fff;
        padding: 20px 40px;
        border-radius: 4px;
        gap: 10px;

        .logo {
            font-size: 2.4rem;
            font-weight: 600;
            color: $primary-color;
        }

        .title {
            font-size: 1.4rem;
            color: $primary-color;
            font-weight: 400;
        }

        form {
            display: flex;
            flex-direction: column;
            gap: 12px;
        }        
    }

    .switch-form {
        font-size: 1.2rem;
        font-weight: 400;
        color: $primary-color;
        margin-top: 6px;
    }
}

form {
    input {
        padding: 8px;
        border: none;
        outline: none;
        
        &:not([type="file"]) {
            width: 250px;
            border-bottom: 1px solid $primary-color;
        }

        &::placeholder {
            color: rgb(175, 175, 175);
        }    
    }

    #file {
        display: none;
    }

    .file-label {
        display: flex;
        align-items: center;
        cursor: pointer;
        width: 50%;

        .upload-icon {
            font-size: 2rem;
            color: lightblue;
        }

        .upload-decript-text {
            font-size: 1.2rem;
            color: $primary-color;
            margin-left: 8px;
        }
    }

    button {
        font-weight: 500;
        background-color: #7b96ec;
        padding: 10px;
        border: none;
        border-radius: 2px;
        color: white;
        cursor: pointer;
    }

    
}


