.search {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid gray;

    .search-form {
        input {
            width: 100%;
            background-color: transparent;
            border: none;
            outline: none;
            color: white;
            padding: 10px 12px;
            font-size: 1.3rem;

            &::placeholder {
                color: lightgray;
            }
        }

        border-bottom: 1px solid gray;
    }

    .user-chat {
        padding: 10px;
        display: flex;
        align-items: center;
        gap: 12px;
        color: #ddddf7;
        cursor: pointer;
        transition: 200ms;

        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: center;

        }

        .user-info {

        }

        &:hover {
            background-color: #2f2d52;
        }
    }

    

}

