.user-chat {
    padding    : 10px;
    display    : flex;
    align-items: center;
    gap        : 12px;
    color      : #ddddf7;
    cursor     : pointer;
    transition : 200ms;

    img {
        width        : 40px;
        height       : 40px;
        border-radius: 50%;
        object-fit   : center;

    }

    .user-info {
        span {
        }

        p {
            margin-top: 4px;
            font-size: 1.3rem;
            color: lightgray;
        }
    }

    &:hover {
        background-color: #2f2d52;
    }
}