.input {
    display: flex;
    align-items: center;
    height: 50px;

    input {
        height: 100%;
        padding: 12px;
        border: none;
        outline: none;
        background-color: #fff;
        color: #2f2d52;
        flex: 1;

        &::placeholder {
            color: lightgray;
        }
    }

    .actions {
        height: 100%;
        display: flex;
        align-items: center;
        gap: 12px;
        padding: 12px;
        background-color: #fff;
        color: #2f2d5280;

        label {
            cursor: pointer;
        }

        input {
            display: none;
        }

        button {
            color: #fff;
            background-color: #8da4f1;
            border: none;
            padding: 8px 10px;
            border-radius: 3px;
            cursor: pointer;
        }
    }
}