.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #2f2d52;
    height: 50px;
    padding: 10px;
    color: #ddddf7;

    .logo {
        font-weight: 600;
    }

    .user {
        display: flex;
        align-items: center;
        gap: 10px;
        border: none;

        img {
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background-color: #ddddf7;
            object-fit: cover;
        }

        button {
            font-size: 1rem;
            background-color: #5d5b8d;
            color: #ddddf7;
            border: none;
            padding: 4px;
            border-radius: 3px;
            cursor: pointer;
        }
    }

}

